.App {
  min-width: 100%;
  background: #004650;
  color: #fffffe;
}
.container {
  border: 2px solid red;
}
.chart {
}
.events {
}

.CitySearch {
  display: flex;
  flex-direction: column;
}

.numberOfEvents {
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 1.3em;
  max-width: 80%;
  margin: 1.2em auto;
}
.label {
  margin: 0 1.2em;
}

#numberOfEvents__input {
  margin-right: 1em;
}
.suggestions {
  align-self: center;
}
.suggestion {
  margin-bottom: 1em;
  list-style: none;
  border: 1px solid red;
  width: 100%;
  border-radius: 5px;
  background: #f9bc60;
  color: #001e1d;
  padding: 0.5em;
  text-align: center;
}
.suggestion:hover {
  cursor: pointer;
}
.EventList {
  list-style: none;
  display: flex;
  flex-direction: column;
}

.event {
  display: flex;
  justify-content: center;
  flex: 1 1 100px;
  list-style: none;
  max-width: 80%;
  align-self: center;
  margin: 2em auto;
  border: 2px solid #181a19;
  border-radius: 10px;
}
.event__details {
  text-align: center;
  align-self: center;
  margin-right: 10px;
}
.event__chart {
  border: 1px solid red;
  align-self: center;
  justify-content: center;
  align-content: center;
  margin-right: 10px;
}
.event__information {
}
