  line-height: 40px;
  font-size: 1.2em;
  padding: 10px 10px;
} */

.event__date {
  text-align: center;
}
.details-btn {
  margin: 2em;
  font-size: 0.6em;
  padding: 1em 4em;
  background: #f9bc60;
}
.details-btn:hover {
  cursor: pointer;
}
a {
  text-decoration: none;
  color: #f9bc60;
  margin-left: 10px;
}

.eventInformation {
  color: #abd1c6;
  text-align: left;
  margin-left: 10px;
}

